// -------------------------
%linkText{ color: $linkColor; text-decoration: none; box-shadow:$linkColor 0 2px 0 -1px;
  &:hover, &:focus{color: $linkColor;}
  &:hover{text-decoration: none; box-shadow:$linkColor 0 3px 0 -1px;}
  &:focus{text-decoration: none; box-shadow:$linkColor 0 0 0 1px;}
}